<template>
	<view style="width: 100%;">
		<view style="background: linear-gradient(to top right,#77f7de,#4ddaf0);height: 100rpx;width: 100%;display: flex;flex-direction: column-reverse;">
			<view style="display: flex;flex-direction: row;justify-content: center;margin-bottom: 5px;">
				<!-- <image src="../../static/login/jiantou.png" style="width: 10px;" mode="widthFix"></image> -->
				<text style="color: #fff;">登录</text>
				
			</view>
		</view>
		<view style="padding: 10px;display: flex;flex-direction: column;">
			<input style="height: 20px;" disabled="true"/>
			<text style="font-size: 30px;color: #11b3a6;font-weight: bold;">手机号登录</text>
			
			<text style="font-size: 14px;color: #a0d9d5;margin-top: 50rpx;font-weight: bold;">手机号</text>
			<!-- <view style="width: ;"></view> -->
			<input v-model="phone" style="background-color: #f9f9f9;border-radius: 10px;height: 40px;margin-top: 5px;"/>
			
			<view style="display: flex;flex-direction: row;align-items: center;">
				<text style="font-size: 14px;color: #a0d9d5;margin-top: 50rpx;font-weight: bold;">验证码</text>
			</view>
			
			<view style="display: flex;flex-direction: row;justify-content: space-between;align-items: center;">
				<input maxlength="6" v-model="yzm" style="background-color: #f9f9f9;border-radius: 10px;height: 40px;margin-top: 5px;width: 70%;"/>
				<view v-if="showText" @click="getCode()" class="button_login">
					发送验证码
				</view>
				<view v-else class="button_login">
					{{second}}
				</view>
			</view>
			
			
			<view style="display: flex;flex-direction: row;margin-top: 20px;width: 100%;align-items: center;justify-content: space-between;">
				<view @click="login()" style="background: linear-gradient(to top right,#4ddaf0,#77f7de);width: 250rpx;height: 40px;display: flex;flex-direction: column;justify-content: center;border-radius: 20px;text-align: center;">
					<text style="color: #fff;">登录</text>
				</view>
				
				<text @click="jump_pwd()" style="color: #7fccc6;">忘记密码</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				phone: '', //手机号码
				second: 60, //默认60秒
				showText: true, //判断短信是否发送
				yzm: '' ,//验证码
				isRotate:false
			}
		},
		mounted() {
			uni.setNavigationBarColor({
				backgroundColor:'#77f7de'
			})
		},
		methods: {
			jump_pwd(){
				uni.navigateTo({
					url:'/pages/login/forget'
				})
			},
			getCode() {
				var that = this;
				if (!that.phone) {
					uni.showToast({ title: '请输入手机号', icon: 'none' });
					return;
				}
				if (!/^[1][3,4,5,7,8,9][0-9]{9}$/.test(that.phone)) {
					uni.showToast({ title: '请输入正确手机号', icon: 'none' });
					return;
				}
				var interval = setInterval(() => {
					that.showText = false;
					var times = that.second - 1;
					//that.second = times<10?'0'+times:times ;//小于10秒补 0
					that.second = times;
				}, 1000);
				setTimeout(() => {
					clearInterval(interval);
					that.second = 60;
					that.showText = true;
				}, 60000);
				//这里请求后台获取短信验证码
				uni.request({
					url:that.server_url+'/sendsms?user='+that.phone,
					method:'GET',
					success: function(res) {
						that.showText = false;
					}
				});
			},
			login() {
				var that = this;
				if (!that.phone) {
					uni.showToast({ title: '请输入手机号', icon: 'none' });
					return;
				}
				if (!/^[1][3,4,5,7,8,9][0-9]{9}$/.test(that.phone)) {
					uni.showToast({ title: '请输入正确手机号', icon: 'none' });
					return;
				}
				
				if (that.yzm=="") {
					uni.showToast({ title: '请输入验证码', icon: 'none' });
					return;
				}
				if (that.yzm.length!=6) {
					uni.showToast({ title: '验证码格式错误', icon: 'none' });
					return;
				}
				console.log(this.server_url+'/loginbySms?user='+this.phone+'&code='+this.yzm);
				uni.request({
					url:this.server_url+'/loginbySms?user='+this.phone+'&code='+this.yzm,
					method:'GET',
					success: (res) => {
						if(res.data.result=="登录成功"){
							uni.setStorage({
								key:'token',
								data:res.data.token
							});
							uni.setStorage({
								key:'user',
								data:this.phone
							});
							
							uni.setStorageSync('gender',res.data.gender);
							
							//判断是不是管理员
							if(this.phone=='admin'){
								uni.setStorage({
									key:'admin',
									data:true
								});
							}
							
							uni.switchTab({
								url:'../navigation/ShouYe/ShouYe'
							})
						}else{
							this.yzm=""
						};
						uni.showToast({
							icon:'none',
							position:'bottom',
							title:res.data.result
						});
					}
				})
			}
		
		}
	}
</script>

<style>
	.button_login{
		width: 100px;
		border-radius: 20px;
		background: linear-gradient(to top right,#4ddaf0,#77f7de);
		color: #fff;
		display: flex;
		flex-direction: row;
		justify-content: center;
	}
</style>
